import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { Link } from "react-router-dom";
import { useEthers } from "@usedapp/core";

import { fetchCollectionNFTs } from "../contracts/helpers";

import {
  ADDRESS_BANNER_NFT_CONTRACT,
  MAX_TOKENS_TO_FETCH,
} from "../types/consts";

import "../styles/collection.css";
import "../styles/common.css";

export function CollectionGrid() {
  const { t } = useTranslation();
  const { library } = useEthers();

  const [collectionNFTs, setCollectionNFTs] = useState<Array<any> | null>(null);

  useEffect(() => {
    fetchCollectionNFTs(library, MAX_TOKENS_TO_FETCH, setCollectionNFTs);

    return function cleanup() {
      setCollectionNFTs(null);
    };
  }, [library]);

  function _renderNFT(idx: number, nft: any) {
    return (
      <Link className="nft" key={idx} to={"/collection/" + nft["tokenId"]}>
        <img src={nft["image"].toString()} alt={nft["name"]} />
        <div className="overlay">
          <p className="text-sm font-medium">{nft["name"]}</p>
          <p className="text-xs">
            {t("collection:token", { tokenId: nft["tokenId"] })}&nbsp;
            {t("collection:owner", { owner: nft["owner"] })}
          </p>
        </div>
      </Link>
    );
  }

  function _renderNFTs() {
    let nfts: JSX.Element[] = [];

    if (collectionNFTs != null) {
      // only show (up to) the most recent 88 tokens
      const n = collectionNFTs.length;
      const k = 88;
      const m = Math.max(n - k, 0);

      for (let i = n - 1; i >= m; i--) {
        nfts.push(_renderNFT(i, collectionNFTs[i]));
      }

      return nfts;
    } else {
      return (
        <div className="min-h-screen col-span-2 mx-10 mt-20 text-center text-2xl">
          {t("collection:loading")}
        </div>
      );
    }
  }

  return (
    <div className="md:grid md:grid-cols-2 md:grid-flow-row">
      {/* browse the collection */}
      {collectionNFTs && collectionNFTs.length > 0 ? (
        <div className="md:col-span-2 text-center">
          {/* link to marketplaces */}
          <div className="inline-block p-4 my-8 border-solid border-2 border-bnft-dark-gray">
            <p>
              {t("collection:browse")}
              <a
                className="mx-1 link-underline"
                href={
                  "https://looksrare.org/collections/" +
                  ADDRESS_BANNER_NFT_CONTRACT
                }
                target="_blank"
                rel="noreferrer"
              >
                {t("collection:looksrare")}
              </a>
              |
              <a
                className="mx-1 link-underline"
                href={"https://opensea.io/collection/bannernfts-1"}
                target="_blank"
                rel="noreferrer"
              >
                {t("collection:opensea")}
              </a>
            </p>
            <p className="text-xs">{t("collection:cap")}</p>
          </div>
        </div>
      ) : (
        <div></div>
      )}

      {/* the tokens, but also a loading message */}
      {collectionNFTs && collectionNFTs.length === 0 ? (
        <div className="md:col-span-2 text-center mt-10">
          <img src="/sadpanda.jpeg" alt="sad" className="inline" />
          <p className="text-sm mt-1">{t("collection:empty")}</p>
        </div>
      ) : (
        _renderNFTs()
      )}

      {/* bye msg */}
      {collectionNFTs && collectionNFTs.length > 0 ? (
        <p className="md:col-span-2 text-center mt-10 text-sm">
          {t("collection:bye")}
        </p>
      ) : (
        <></>
      )}
    </div>
  );
}
